import React from "react"
import Navbar from "../components/navbar"
import Container from 'react-bootstrap/Container'

export default function UltraStack() {
  return (
    <div>
        <link
            rel="stylesheet"
            href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
            integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
            crossorigin="anonymous"
        />

        <Navbar items={['Blog', 'Resume', 'Projects']} links={['/', '/resume', '/projects']} projects={['Ultra Stack', 'Shaders', 'Puzzle Game', 'IK Demos', 'Spline Editor']}/>    

        
        <Container fluid>
            <br/>
            <h1>Ultra Stack User Guide</h1>
            <p>
              UltraStack is a casual mobile game where the player attempts to stack blocks as high as possible. 
              I finished development and released the game on the Apple App Store and Google Play Store in early 2019. 
              The app has been archived and is no longer available, but you can download the PC version below. 
              It was developed using Unity3D and C#. 
            </p>

            <h2>Installation</h2>
            <p>
              Follow the build instructions on <a href="https://github.com/Onlinerocker/UltraStack">GitHub</a> or 
              download the executable <a href="../downloads/ultra_stack.zip">here</a>.
            </p>

            <h2>How to Play</h2>
            <p>
              The goal of UltraStack is to create the tallest tower possible. 
              player's score is equal to the tower's height. 
              However, the game utilizes the Havoc physics engine, making it possible for an unstable tower to fall over. 
              If the tower falls over, the game ends. 
              The most successful towers have a balance of stability and height. 
            </p>

            <h2>Controls</h2>
            <p>
              Use the left and right arrow keys to move a block left and right, respectively. 
              To make a block drop faster, hold the down arrow key. 
              To rotate a block, tap the Z key.
            </p>
        </Container>
    </div>
  )
}
